<template>
  <el-card class="box-card card-myacconut">
    <div slot="header" class="clearfix">
      <span>顯示帳號</span>
    </div>
    <div class="account-item item-odd">
      <span class="item-label">{{ myaccount.yesterDayEquity.label }}</span>：
      <span :class="['item-value', { 'positive': myaccount.yesterDayEquity.value >= 0, 'negative': myaccount.yesterDayEquity.value < 0 }]">{{ formatNumber(myaccount.yesterDayEquity.value) }}</span>
    </div>
    <div class="account-item">
      <span class="item-label">{{ myaccount.todayIncome.label }}</span>：
      <span :class="['item-value', { 'positive': myaccount.todayIncome.value >= 0, 'negative': myaccount.todayIncome.value < 0 }]">{{ formatNumber(myaccount.todayIncome.value) }}</span>
    </div>
    <div class="account-item item-odd">
      <span class="item-label">{{ myaccount.remainingPrice.label }}</span>：
      <span :class="['item-value', { 'positive': myaccount.remainingPrice.value >= 0, 'negative': myaccount.remainingPrice.value < 0 }]">{{ formatNumber(myaccount.remainingPrice.value) }}</span>
    </div>
    <div class="account-item">
      <span class="item-label">{{ myaccount.forcePrice.label }}</span>：
      <span :class="['item-value', { 'positive': myaccount.forcePrice.value >= 0, 'negative': myaccount.forcePrice.value < 0 }]">{{ formatNumber(myaccount.forcePrice.value) }}</span>
    </div>
    <div class="account-item item-odd">
      <span class="item-label">{{ myaccount.promisePrise.label }}</span>：
      <span :class="['item-value', { 'positive': myaccount.promisePrise.value >= 0, 'negative': myaccount.promisePrise.value < 0 }]">{{ formatNumber(myaccount.promisePrise.value) }}</span>
    </div>
    <div class="account-item">
      <span class="item-label">{{ myaccount.totalEquity.label }}</span>：
      <span :class="['item-value', { 'positive': myaccount.totalEquity.value >= 0, 'negative': myaccount.totalEquity.value < 0 }]">{{ formatNumber(myaccount.totalEquity.value) }}</span>
    </div>
  </el-card>
</template>

<script>
export default {
  name: 'MyAccount',
  data() {
    return {
      myaccount: {
        yesterDayEquity: {
          label: '昨日權益數',
          value: _.random(-100000, 10000000),
        },
        todayIncome: {
          label: '今日損益',
          value: _.random(-10000, 10000),
        },
        remainingPrice: {
          label: '可用餘額',
          value: _.random(0, 10000000),
        },
        forcePrice: {
          label: '強平額度',
          value: _.random(-1000000, 100000),
        },
        promisePrise: {
          label: '留倉保證金',
          value: _.random(0, 10000),
        },
        totalEquity: {
          label: '權益總數',
          value: _.random(1000000, 10000000),
        },
      }
    }
  },
  methods: {
    formatNumber(number) {
      return number.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")
    },
  }
}
</script>

<style lang="scss">
.box-card {
  &.card-myacconut {
    background: #000000;
    color: #ffffff;
    margin: 5px;
    border-bottom-color: #ffffff;
    .el-card__header {
      padding: 5px 20px;
      font-size: 14px;
    }
    .el-card__body {
      padding: 0;
      .account-item {
        font-size: 12px;
        padding: 5px;
        border-bottom-color: #434343;
        &.item-odd {
          background-color: #313131;
        }
        .item-label {
          display: inline-block;
          width: 35%;
        }
        .item-value {
          font-family: "Krungthep";
          font-size: 20px;
          &.positive {
            color: #ff598c;
          }
          &.negative {
            color: #b0df09;
          }
        }
      }
    }
  }
}
</style>



