<template>
  <el-card class="box-card card-product-strike-price">
    <div slot="header" class="clearfix">
      <span>報價明細 [{{ itemName }}]</span>
    </div>
    <el-table
    :data="transactions"
    border
    header-cell-class-name="table-header-cell"
    cell-class-name="table-cell"
    :row-class-name="tableRowClassName">
    <el-table-column
      prop="time"
      label="時間"
      min-width="65"
      header-align="center"
      align="left">
    </el-table-column>
    <el-table-column
      prop="price"
      label="價格"
      min-width="55"
      header-align="center"
      align="right">
    </el-table-column>
    <el-table-column
      prop="count"
      label="單量"
      min-width="60"
      header-align="center"
      align="right">
    </el-table-column>
  </el-table>
  </el-card>
</template>

<script>
export default {
  name: 'ProductStrikePrices',
  data() {
    return {
      itemName: '德國DAX9',
      transactions: []
    }
  },
  mounted() {
    let vm = this
    vm.generateFakeData()
  },
  methods: {
    generateFakeData() {
      let vm = this
      for (let i = 0; i <= 20; i++) {
        let data = {
          time: vm.randomTime(),
          price: _.random(1, 100000),
          count: _.random(1, 10),
        }
        vm.transactions.push(data)
      }
    },
    randomTime() {
      let start = new Date(2017, 0, 1)
      let end = new Date()
      let randomTime = new Date(
        start.getTime() + Math.random() * (end.getTime() - start.getTime())
      )
      var hour = randomTime.getHours()
      var minute = randomTime.getMinutes()
      var second = randomTime.getSeconds()
      var finalTime = `${hour}:${minute}:${second}`
      return finalTime
    },
    tableRowClassName({ row, rowIndex }) {
      let className = ((rowIndex + 1) % 2) === 1 ? 'row-odd' : 'row-even'
      return className
    },
  },
}
</script>

<style lang="scss">
.box-card {
  &.card-product-strike-price {
    margin: 5px;
    display: flex;
    flex-direction: column;
    flex: 1;
    font-size: 12px;
    background-color: #000000;
    color: #ffffff;
    .el-card__header {
      padding: 5px 20px;
      font-size: 14px;
    }
    .el-card__body {
      display: flex;
      flex-direction: column;
      padding: 0;
      .el-table {
        font-size: 12px;
        overflow: auto;
        color: #ffffff;
        border-color: #434343;
        .el-table--border {
          border: none;
        }
        tr {
          &:hover {
            .table-cell {
              background-color: #616161;
            }
          }
        }
        .row-odd {
          background: #000000;
        }
        .row-even {
          background: #313131;
        }
        .table-header-cell {
          border-color: #434343;
          background-color: #767676;
          color: #ffffff;
          padding: 0;
          .cell {
            padding-right: 6px;
            padding-left: 6px;
            line-height: 2;
          }
        }
        .table-cell {
          padding: 0;
          border-color: #434343;
          .cell {
            padding-right: 6px;
            padding-left: 6px;
            line-height: 2;
          }
        }
      }
    }
  }
}
</style>



