<template>
  <el-card class="box-card card-buy">
    <div slot="header">
      <span>購買商品</span>
    </div>
    <el-row class="card-body">
      <el-row class="buy-checkbox-options">
        <el-checkbox v-model="lockProduct">鎖定商品</el-checkbox>
        <el-checkbox v-model="orderWithoutCheck">下單不確認</el-checkbox>
      </el-row>
      <el-row class="verticle-middle">
        <el-col :span="5" class="verticle-middle">
          <span>商品</span>
        </el-col>
        <el-col :span="19" class="verticle-middle">
          <el-select v-model="selectedProduct" placeholder="請選擇商品" size="mini">
            <el-option
              v-for="product in products"
              :key="product.value"
              :label="product.label"
              :value="product.value">
            </el-option>
          </el-select>
        </el-col>
      </el-row>
      <el-row>
        <el-radio-group v-model="buyPrice">
          <el-radio :label="1000">市價</el-radio>
          <el-radio :label="2000">收盤價</el-radio>
        </el-radio-group>
      </el-row>
      <el-row class="verticle-middle">
        <el-col :span="5" class="verticle-middle">
          <span>限價</span>
        </el-col>
        <el-col :span="19" class="verticle-middle">
          <el-input-number v-model="buyPrice" :min="1" controls-position="right" size="mini"></el-input-number>
          <el-button class="el-button--extra-mini">限</el-button>
        </el-col>
      </el-row>
      <el-row>
        <el-button-group>
          <el-button class="el-button--extra-mini">1</el-button>
          <el-button class="el-button--extra-mini">2</el-button>
          <el-button class="el-button--extra-mini">3</el-button>
          <el-button class="el-button--extra-mini">4</el-button>
          <el-button class="el-button--extra-mini">5</el-button>
          <el-button class="el-button--extra-mini">設</el-button>
        </el-button-group>
      </el-row>
      <el-row class="verticle-middle">
        <el-col :span="5" class="verticle-middle">
          <span>口數</span>
        </el-col>
        <el-col :span="19" class="verticle-middle">
          <el-input-number v-model="buyCount" :min="1" controls-position="right" size="mini"></el-input-number>
          <el-button class="el-button--extra-mini">全平</el-button>
        </el-col>
      </el-row>
      <el-row :gutter="10">
        <el-col :span="12">
          <el-button class="el-button-block el-button-purple" size="mini">多單</el-button>
        </el-col>
        <el-col :span="12">
          <el-button class="el-button-block el-button-yellow" size="mini">空單</el-button>
        </el-col>
      </el-row>
    </el-row>
  </el-card>
</template>

<script>
export default {
  name: 'Buy',
  data() {
    return {
      lockProduct: false,
      orderWithoutCheck: false,
      products: [
        {
          value: '元大台灣50',
          label: '元大台灣50'
        },
        {
          value: '元大中型100',
          label: '元大中型100'
        },
        {
          value: '富邦科技',
          label: '富邦科技'
        },
        {
          value: '元大電子',
          label: '元大電子'
        },
        {
          value: '元大台商50',
          label: '元大台商50'
        },
        {
          value: '元大MSCI金融',
          label: '元大MSCI金融'
        },
        {
          value: '元大高股息',
          label: '元大高股息'
        },
        {
          value: '富邦摩台',
          label: '富邦摩台'
        },
        {
          value: '富邦發達',
          label: '富邦發達'
        },
        {
          value: '富邦金融',
          label: '富邦金融'
        },
        {
          value: '元大寶滬深',
          label: '元大寶滬深'
        },
        {
          value: '元大MSCI台灣',
          label: '元大MSCI台灣'
        },
        {
          value: '永豐臺灣加權',
          label: '永豐臺灣加權'
        },
        {
          value: '富邦上証',
          label: '富邦上証'
        },
        {
          value: '元大上證50',
          label: '元大上證50'
        },
        {
          value: 'FH滬深',
          label: 'FH滬深'
        },
        {
          value: '富邦台50',
          label: '富邦台50'
        },
        {
          value: '元大台灣50正2',
          label: '元大台灣50正2'
        },
        {
          value: '元大台灣50反1',
          label: '元大台灣50反1'
        },
        {
          value: '富邦上証正2',
          label: '富邦上証正2'
        },
      ],
      selectedProduct: '',
      buyPrice: 0,
      buyCount: 0,
    };
  },
}
</script>

<style lang="scss">
.box-card {
  &.card-buy {
    margin: 5px;
    overflow: auto;
    .el-card__header {
      padding: 5px 20px;
      font-size: 14px;
    }
    .el-card__body {
      padding: 5px;
      font-size: 12px;
      .buy-checkbox-options {
        .el-checkbox {
          display: inline;
          &+.el-checkbox {
            margin-left: 5px;
          }
          .el-checkbox__label {
            padding-left: 5px;
          }
        }
      }
      span {
        font-size: 12px;
      }
    }
  }
  .el-row {
    margin-bottom: 5px;
    &.verticle-middle {
      display: flex;
      align-self: center;
    }
    .el-col {
      &.verticle-middle {
        display: flex;
        align-self: center;
      }
    }
  }
}
.el-button {
  &.el-button-block {
    width: 100% !important;
  }
  &.el-button-purple {
    background: #a877ff;
    color: #ffffff;
  }
  &.el-button-yellow {
    background: #ffc34f;
    color: #ffffff;
  }
  &.el-button--extra-mini {
    padding: 5px 10px;
  }
}
</style>



