<template>
  <div class="announcement">
    <img src="../assets/speaker.png">
    <marquee direction="left" scrollamount="10">{{ message }}</marquee>
  </div>
</template>

<script>
export default {
  name: 'Announcement',
  data() {
    return {
      message: '【公告】歐洲自3月25日(日)起日光節約時間(夏令)開始，所有歐洲盤商品之開收盤時間，將較目前提早一小時。',
    }
  },
}
</script>

<style lang="scss">
.announcement {
  display: flex;
  align-items: center;
  color: #e9df53;
  padding: 5px;
}
</style>



