<template>
  <el-table
    :data="stocksData"
    class="stock-data-table"
    border
    header-cell-class-name="table-header-cell"
    :cell-class-name="getCellClasses"
    :row-class-name="tableRowClassName"
    max-height="400">
    <el-table-column
      fixed
      prop="name"
      label="商品名稱"
      min-width="90"
      class-name="table-first-column">
    </el-table-column>
    <el-table-column
      prop="longPosition"
      label="倉位多"
      min-width="40"
      align="center">
    </el-table-column>
    <el-table-column
      prop="shortPosition"
      label="倉位空"
      min-width="40"
      align="center">
    </el-table-column>
    <el-table-column
      prop="bidPrice"
      header-align="center"
      min-width="55"
      align="right"
      label="買進價">
    </el-table-column>
    <el-table-column
      prop="askPrice"
      label="買出價"
      min-width="55"
      header-align="center"
      align="right">
    </el-table-column>
    <el-table-column
      prop="strikePrice"
      label="成交價"
      min-width="55"
      header-align="center"
      align="right">
    </el-table-column>
    <el-table-column
      prop="rise"
      label="漲跌"
      min-width="40"
      header-align="center"
      align="right">
    </el-table-column>
    <el-table-column
      prop="risePercent"
      label="漲跌％"
      min-width="50"
      header-align="center"
      align="right">
    </el-table-column>
    <el-table-column
      prop="orderCount"
      label="單量"
      min-width="60"
      header-align="center"
      align="right">
    </el-table-column>
    <el-table-column
      prop="volume"
      label="總量"
      min-width="55"
      header-align="center"
      align="right">
    </el-table-column>
    <el-table-column
      prop="yesterdayClosePrice"
      label="昨收價"
      min-width="55"
      header-align="center"
      align="right">
    </el-table-column>
    <el-table-column
      prop="openPrice"
      label="開盤價"
      min-width="55"
      header-align="center"
      align="right">
    </el-table-column>
    <el-table-column
      prop="highestPrice"
      label="最高價"
      min-width="55"
      header-align="center"
      align="right">
    </el-table-column>
    <el-table-column
      prop="lowestPrice"
      label="最低價"
      min-width="55"
      header-align="center"
      align="right">
    </el-table-column>
    <el-table-column
      prop="time"
      label="時間"
      min-width="65"
      header-align="center"
      align="right">
    </el-table-column>
    <el-table-column
      prop="type"
      min-width="50"
      label="交易類型"
      header-align="center"
      align="center">
    </el-table-column>
    <el-table-column
      prop="lastClosingCost"
      label="最後成交價"
      min-width="70"
      header-align="center"
      align="right">
    </el-table-column>
    <el-table-column
      prop="lastTradingDay"
      label="最後交易日"
      min-width="70"
      header-align="center"
      align="right">
    </el-table-column>
    <el-table-column
      prop="instructions"
      min-width="35"
      header-align="center"
      align="center"
      label="說明">
      <template slot-scope="scope">
        <el-button
          class="btn-explain"
          type="text"
          size="mini"
          @click="handleEdit(scope.$index, scope.row)">說明</el-button>
      </template>
    </el-table-column>
  </el-table>
</template>

<script>
export default {
  name: 'StocksDataTable',
  data() {
    return {
      stockDataTemplate: {
        name: '',
        longPosition: '',
        shortPosition: '',
        bidPrice: '',
        askPrice: '',
        strikePrice: '',
        rise: '',
        risePercent: '',
        orderCount: '',
        volume: '',
        yesterdayClosePrice: '',
        openPrice: '',
        highestPrice: '',
        lowestPrice: '',
        time: '',
        type: '',
        lastClosingCost: '',
        lastTradingDay: '',
        instructions: '',
      },
      products: [
        '元大台灣50',
        '元大中型100',
        '富邦科技',
        '元大電子',
        '元大台商50',
        '元大MSCI金融',
        '元大高股息',
        '富邦摩台',
        '富邦發達',
        '富邦金融',
        '元大寶滬深',
        '元大MSCI台灣',
        '永豐臺灣加權',
        '富邦上証',
        '元大上證50',
        'FH滬深',
        '富邦台50',
        '富邦上証+R',
        '元大台灣50正2',
        '元大台灣50反1',
        '富邦上証正2',
      ],
      transTypes: ['禁止新單', '停止交易', '正常交易'],
      stocksData: [],
    }
  },
  mounted() {
    let vm = this
    vm.generateFakeData()
  },
  methods: {
    generateFakeData() {
      let vm = this
      _.forEach(vm.products, function (productName) {
        let data = {
          name: productName,
          longPosition: _.sample([1, '']),
          shortPosition: _.sample([1, '']),
          bidPrice: _.random(1, 100000),
          askPrice: _.random(1, 100000),
          strikePrice: _.random(1, 100000),
          rise: _.random(1, 500),
          risePercent: (_.random(1, true) * 100).toFixed(2) + '%',
          orderCount: _.random(1, 5000),
          volume: _.random(1, 1000000),
          yesterdayClosePrice: _.random(1, 100000),
          openPrice: _.random(1, 100000),
          highestPrice: _.random(1, 100000),
          lowestPrice: _.random(1, 100000),
          time: vm.randomDate(),
          type: _.sample(vm.transTypes),
          lastClosingCost: _.random(1, 100000),
          lastTradingDay: vm.randomDate(),
          instructions: '',
        }
        vm.stocksData.push(data)
      })
    },
    randomDate() {
      let start = new Date(2017, 0, 1)
      let end = new Date()
      let randomDate = new Date(
        start.getTime() + Math.random() * (end.getTime() - start.getTime())
      )
      var day = randomDate.getDay()
      var month = randomDate.getMonth() + 1
      var year = randomDate.getFullYear()
      var finalDate = `${year}/${month}/${day}`
      return finalDate
    },
    getCellClasses({ row, column, rowIndex, columnIndex }) {
      let classes = ['table-cell']
      let randomIsRise = _.sample([true, false, 'equal'])

      if (_.includes([3, 4, 5, 6, 7, 11, 12, 13, 16], columnIndex)) {
        let riseClass = randomIsRise ? 'stock-rise-text' : 'stock-fall-text'
        classes.push(riseClass)
      }

      if (columnIndex == 1 && row.longPosition == 1) {
        classes.push(_.sample(['stock-rise-bg', 'stock-fall-bg']))
      }

      if (columnIndex == 2 && row.shortPosition == 1) {
        classes.push(_.sample(['stock-rise-bg', 'stock-fall-bg']))
      }

      let strClasses = classes.join(' ')

      return strClasses
    },
    tableRowClassName({ row, rowIndex }) {
      let className = ((rowIndex + 1) % 2) === 1 ? 'row-odd' : 'row-even'
      return className
    }
  }
}
</script>

<style lang="scss">
.el-table {
  &.stock-data-table {
    font-size: 12px;
    color: #ffffff;
    border-color: #434343;
    .table-header-cell {
      border-color: #434343;
      background-color: #767676;
      color: #ffffff;
      padding: 0;
      .cell {
        padding-right: 0px;
        padding-left: 0px;
        line-height: 2;
      }
    }
    .hover-row {
      .table-cell {
        background-color: #616161;
      }
    }
    .row-odd {
      background: #000000;
    }
    .row-even {
      background: #313131;
    }
    .table-first-column {
      padding: 0;
    }
    .table-cell {
      padding: 0;
      border-color: #434343;
      &.stock-rise-text {
        color: #ff598c;
      }
      &.stock-fall-text {
        color: #b0df09;
      }
      &.stock-rise-bg {
        background: #ff598c;
        color: #000000;
      }
      &.stock-fall-bg {
        background: #b0df09;
        color: #000000;
      }
      .cell {
        padding-right: 0px;
        padding-left: 0px;
        height: 24px;
        .btn-explain {
          font-size: 12px;
          padding: 0;
        }
      }
    }
  }
}
</style>



