<template>
  <div class="main">
    <Navbar/>
    <el-row class="row-main">
      <el-col :span="20" class="main-left">
        <el-row>
          <Announcement/>
        </el-row>
        <el-row class="row-stockdatatable">
          <StocksDataTable/>
        </el-row>
        <el-row class="row-datatabs">
          <DataTabs/>
        </el-row>
      </el-col>
      <el-col :span="4" class="main-right">
        <ProdudctStrikePrices/>
        <MyAccount/>
        <Buy/>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import Navbar from '@/components/Navbar'
import StocksDataTable from '@/components/StocksDataTable'
import ProdudctStrikePrices from '@/components/ProdudctStrikePrices'
import MyAccount from '@/components/MyAccount'
import Buy from '@/components/Buy'
import DataTabs from '@/components/DataTabs'
import Announcement from '@/components/Announcement'

export default {
  name: 'Index',
  components: {
    StocksDataTable,
    Navbar,
    ProdudctStrikePrices,
    MyAccount,
    Buy,
    DataTabs,
    Announcement,
  },
}
</script>

<style lang="scss">
@import "~amstock3/amcharts/style.css";

.main {
  height: 100%;
  display: flex;
  flex-direction: column;
  .row-main {
    height: 100%;
    display: flex;
    flex-direction: row;
    align-items: stretch;
    .main-left {
      background: #000000;
      display: flex;
      flex-direction: column;
      .row-datatabs {
        display: flex;
        flex-direction: column;
        flex: 1;
      }
    }
    .main-right {
      background: #000000;
      display: flex;
      flex-direction: column;
    }
  }
}
</style>



