<template>
  <el-row>
    <el-radio-group v-model="orderType" class="order-filter">
      <el-radio :label="1">全部單</el-radio>
      <el-radio :label="2">未成交單</el-radio>
      <el-radio :label="3">已成交單</el-radio>
    </el-radio-group>
    <el-table
      :data="orders"
      border
      height="600">
      <el-table-column
        prop="action"
        label="操作">
      </el-table-column>
      <el-table-column
        prop="id"
        label="序號">
      </el-table-column>
      <el-table-column
        prop="productName"
        label="商品">
      </el-table-column>
      <el-table-column
        prop="fail"
        label="倒">
      </el-table-column>
      <el-table-column
        prop="profitAndLoss"
        label="多空">
      </el-table-column>
      <el-table-column
        prop="commissionPrice"
        label="委託價">
      </el-table-column>
      <el-table-column
        prop="orderCount"
        label="口數">
      </el-table-column>
      <el-table-column
        prop="strikePrice"
        label="成交價">
      </el-table-column>
      <el-table-column
        prop="buyTime"
        label="下單時間">
      </el-table-column>
      <el-table-column
        prop="completedTime"
        label="完成時間">
      </el-table-column>
      <el-table-column
        prop="type"
        label="型別">
      </el-table-column>
      <el-table-column
        prop="lossPoint"
        label="損失點">
      </el-table-column>
      <el-table-column
        prop="profitPoint"
        label="獲利點">
      </el-table-column>
      <el-table-column
        prop="status"
        label="狀態">
      </el-table-column>
    </el-table>
  </el-row>
</template>

<script>
export default {
  name: 'TotalOrdersTable',
  data() {
    return {
      orderType: 1,
      orders: [],
      products: [
        '元大台灣50',
        '元大中型100',
        '富邦科技',
        '元大電子',
        '元大台商50',
        '元大MSCI金融',
        '元大高股息',
        '富邦摩台',
        '富邦發達',
        '富邦金融',
        '元大寶滬深',
        '元大MSCI台灣',
        '永豐臺灣加權',
        '富邦上証',
        '元大上證50',
        'FH滬深',
        '富邦台50',
        '富邦上証+R',
        '元大台灣50正2',
        '元大台灣50反1',
        '富邦上証正2',
      ],
    }
  },
  mounted() {
    let vm = this
    vm.generateFakeData()
  },
  methods: {
    generateFakeData() {
      let vm = this
      _.forEach(vm.products, function (product) {
        let data = {
          action: '',
          id: _.random(1, 10000),
          productName: product,
          fail: '',
          profitAndLoss: _.sample(['多', '空']),
          commissionPrice: _.random(1, 1000000),
          orderCount: _.random(1, 10),
          strikePrice: _.random(1, 1000000),
          buyTime: vm.randomTime(),
          completedTime: vm.randomTime(),
          type: _.sample(['網路下單', '轉新單']),
          lossPoint: '',
          profitPoint: '',
          status: '已成交',
        }
        vm.orders.push(data)
      })
    },
    randomTime() {
      let start = new Date(2017, 0, 1)
      let end = new Date()
      let randomTime = new Date(start.getTime() + Math.random() * (end.getTime() - start.getTime()))
      var hour = randomTime.getHours()
      var minute = randomTime.getMinutes()
      var second = randomTime.getSeconds()
      var finalTime = `${hour}:${minute}:${second}`
      return finalTime
    },
  }
}
</script>

<style lang="scss">
.order-filter {
  margin-bottom: 15px;
}
</style>



