<template>
  <el-tabs class="data-tabs" type="card" @tab-click="handleClick">
    <el-tab-pane label="K 線圖">
      <Candlestick/>
    </el-tab-pane>
    <el-tab-pane label="走勢圖">
      <TrendChart/>
    </el-tab-pane>
    <el-tab-pane label="全部單">
      <TotalOrdersTable/>
    </el-tab-pane>
    <el-tab-pane label="未平倉">
      <UnCoverTable/>
    </el-tab-pane>
    <el-tab-pane label="已平倉">Covered</el-tab-pane>
    <el-tab-pane label="統計">Statistics</el-tab-pane>
  </el-tabs>
</template>

<script>
import Candlestick from '@/components/Candlestick'
import TrendChart from '@/components/TrendChart'
import TotalOrdersTable from '@/components/TotalOrdersTable'
import UnCoverTable from '@/components/UnCoverTable'

export default {
  name: 'DataTabs',
  components: {
    Candlestick,
    TrendChart,
    TotalOrdersTable,
    UnCoverTable,
  },
  data() {
    return {

    }
  },
  methods: {
    handleClick(tab, event) { }
  },
}
</script>

<style lang="scss">
.el-tabs {
  &.data-tabs {
    height: 100%;
    margin: 5px;
    display: flex;
    flex-direction: column;
    .el-tabs__header {
      margin: 0 0 10px;
    }
    .el-tabs__content {
      height: 100%;
      .el-tab-pane {
        height: 100%;
      }
    }
    .el-tabs__item {
      height: 25px;
      line-height: 25px;
      color: #ffffff;
      &.is-active {
        color: #e9df53;
      }
      &:hover {
        color: #e9df53;
      }
    }
  }
}
</style>



