<template>
  <el-row>
    <el-button class="mb-s" type="default" icon="el-icon-document" size="small">市價平倉</el-button>
    <el-table
      :data="orders"
      border
      @selection-change="handleSelectionChange"
      height="600">
      <el-table-column
        type="selection"
        label="全選">
      </el-table-column>
      <el-table-column
        prop="id"
        label="序號">
      </el-table-column>
      <el-table-column
        prop="productName"
        label="商品">
      </el-table-column>
      <el-table-column
        prop="strikePrice"
        label="成交價">
      </el-table-column>
      <el-table-column
        prop="orderCount"
        label="口數">
      </el-table-column>
      <el-table-column
        prop="fee"
        label="手續費">
      </el-table-column>
      <el-table-column
        prop="lossPoint"
        label="損失點">
      </el-table-column>
      <el-table-column
        prop="profitPoint"
        label="獲利點">
      </el-table-column>
      <el-table-column
        prop="action"
        label="操作">
      </el-table-column>
      <el-table-column
        prop="quote"
        label="報價">
      </el-table-column>
      <el-table-column
        prop="UnfairProfitAndLoss"
        label="未平損益">
      </el-table-column>
      <el-table-column
        prop="numberOfDays"
        label="天數">
      </el-table-column>
      <el-table-column
        prop="status"
        label="狀態">
      </el-table-column>
      <el-table-column
        prop="yesterdayIncome"
        label="昨日損益">
      </el-table-column>
    </el-table>
  </el-row>
</template>

<script>
export default {
  name: 'UnCoverTable',
  data() {
    return {
      orderType: 1,
      orders: [],
      products: [
        '元大台灣50',
        '元大中型100',
        '富邦科技',
        '元大電子',
        '元大台商50',
        '元大MSCI金融',
        '元大高股息',
        '富邦摩台',
        '富邦發達',
        '富邦金融',
        '元大寶滬深',
        '元大MSCI台灣',
        '永豐臺灣加權',
        '富邦上証',
        '元大上證50',
        'FH滬深',
        '富邦台50',
        '富邦上証+R',
        '元大台灣50正2',
        '元大台灣50反1',
        '富邦上証正2',
      ],
      selectedOrders: [],
    }
  },
  mounted() {
    let vm = this
    vm.generateFakeData()
  },
  methods: {
    generateFakeData() {
      let vm = this
      _.forEach(vm.products, function (product) {
        let data = {
          action: '',
          id: _.random(1, 10000),
          productName: product,
          fail: '',
          profitAndLoss: _.sample(['多', '空']),
          commissionPrice: _.random(1, 1000000),
          orderCount: _.random(1, 10),
          strikePrice: _.random(1, 1000000),
          buyTime: vm.randomTime(),
          completedTime: vm.randomTime(),
          type: _.sample(['網路下單', '轉新單']),
          lossPoint: '',
          profitPoint: '',
          status: '已成交',
          fee: _.random(1, 1000000),
          quote: _.random(1, 1000000),
          UnfairProfitAndLoss: _.random(1, 10000),
          numberOfDays: _.random(1, 10),
          status: '',
          yesterdayIncome: _.random(1, 1000000),
        }
        vm.orders.push(data)
      })
    },
    randomTime() {
      let start = new Date(2017, 0, 1)
      let end = new Date()
      let randomTime = new Date(start.getTime() + Math.random() * (end.getTime() - start.getTime()))
      var hour = randomTime.getHours()
      var minute = randomTime.getMinutes()
      var second = randomTime.getSeconds()
      var finalTime = `${hour}:${minute}:${second}`
      return finalTime
    },
    toggleSelection(rows) {
      if (rows) {
        rows.forEach(row => {
          this.$refs.selectedOrders.toggleRowSelection(row);
        });
      } else {
        this.$refs.selectedOrders.clearSelection();
      }
    },
    handleSelectionChange(val) {
      this.selectedOrders = val;
    },
  }
}
</script>

<style lang="scss">
.order-filter {
  margin-bottom: 15px;
}
</style>



