<template>
  <el-menu
  class="navnbar-menu"
  mode="horizontal"
  background-color="#303030"
  text-color="#fff"
  active-text-color="#ffd04b">
  <el-menu-item index="1">連線</el-menu-item>
  <el-submenu index="2">
    <template slot="title">檢視</template>
    <el-menu-item index="2-1">會員資訊</el-menu-item>
    <el-menu-item index="2-2">帳戶歷史</el-menu-item>
    <el-menu-item index="2-3">報價明細</el-menu-item>
    <el-menu-item index="2-4">儲值紀錄</el-menu-item>
  </el-submenu>
  <el-submenu index="3">
    <template slot="title">工具</template>
    <el-menu-item index="3-1">試算表</el-menu-item>
    <el-menu-item index="3-2">小算盤</el-menu-item>
  </el-submenu>
  <el-submenu index="4">
    <template slot="title">設定</template>
    <el-menu-item index="4-1">更變密碼</el-menu-item>
    <el-menu-item index="4-2">自訂商品</el-menu-item>
    <el-menu-item index="4-3">版面設定</el-menu-item>
    <el-menu-item index="4-4">設定開盤風控</el-menu-item>
    <el-menu-item index="4-5">貨幣</el-menu-item>
  </el-submenu>
  <el-submenu index="5">
    <template slot="title">設定</template>
    <el-menu-item index="5-1">規則說明</el-menu-item>
    <el-menu-item index="5-2">名稱說明</el-menu-item>
    <el-menu-item index="5-3">商品交易時間</el-menu-item>
  </el-submenu>
  <el-menu-item index="6" class="btn-trail">領取試玩點數</el-menu-item>
</el-menu>
</template>

<script>
export default {
  name: 'Navbar',
}
</script>

<style lang="scss">
.el-menu {
  border: none;
  &.navnbar-menu {
    .el-menu-item {
      height: 40px;
      line-height: 40px;
      &.btn-trail {
        color: #303030 !important;
        background-color: #fff799 !important;
        border-radius: 20px;
        height: 30px;
        line-height: 30px;
        top: 5px;
      }
    }
    .el-submenu {
      .el-submenu__title {
        height: 40px;
        line-height: 40px;
      }
    }
  }
}
</style>



